import Vue from 'vue'
import Router from 'vue-router'
import Board from '@/components/Board'
import Login from '@/components/Login'

Vue.use(Router);

export default new Router({
    mode: "history",
    routes: [
        {
        path: '/board/free',
        name: Board,
        component: Board
        },
        {
        path: '/login',
        name: Login,
        component: Login
        }
    ]
})