<template>
   <div>
       <b-form>
           <!-- 아이디 입력폼 -->
           <b-form-group
                id="group-id"
                label="아이디"
                label-for="input-id"
            >
            <b-form-input
                v-model="userId"
                :state="validation"
                id="input-id"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validation">
                아이디를 5-12자리로 구성하세요.
            </b-form-invalid-feedback>
            <b-form-valid-feedback :state="validation">
                good.
            </b-form-valid-feedback>
            </b-form-group>

            <!-- 비밀번호 입력 폼 -->
           <b-form-group
                id="group-pw"
                label="비밀번호"
                label-for="input-pw"
            >
            <b-form-input
                v-model="userPw"
                id="input-pw"
                type="password"
            >
            </b-form-input>
           </b-form-group>

           <!-- 비밀번호 확인 입력 폼 -->
           <b-form-group
                id="group-pw2"
                label="비밀번호 확인"
                label-for="input-pw2"
            >
            <b-form-input
                v-model="userPw2"
                id="input-pw2"
                type="password"
                :state="password"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="password">
                비밀번호가 일치하지 않습니다.
            </b-form-invalid-feedback>
            <b-form-valid-feedback :state="password">
                good.
            </b-form-valid-feedback>
           </b-form-group>
       </b-form>
   </div>
</template>

<script>
export default {
    data() {
        return {
            userId: '',
            userPw: '',
            userPw2: ''
        }
    },
    computed: {
        validation() {
            return this.userId.length > 4 && this.userId.length < 13
        },
        password() {
            return this.userPw == this.userPw2
        }
    }
}
</script>


<style>
.black-bg {
    width: 100%; height: 100%;
    background: rgba(0, 0, 0, 0.5);
    position: fixed; padding: 20px;
}
.white-bg {
    width: 100%; background: white;
    border-radius: 8px;
    padding: 10px;
}
.flex-fill.mx-xl-5.mb-2{
	margin: 0 auto;
	width : 700px;
	padding-right: 7rem;
	padding-left: 7rem;
}
.container.py-4{
	margin: 0 auto;
	width : 503px;
}
.d-grid.gap-2{
	padding-top: 10px;
    width: 1000px;
}
.bir_yy,.bir_mm,.bir_dd{
	width:170px;
	display:table-cell;
}
.bir_mm+.bir_dd, .bir_yy+.bir_mm{
	padding-left:15px;
}
</style>